.weather-box{
    width: 250px;
    border-radius: 6px;
    box-shadow: 10px -2px 20px 2px rgb(0 0 0/ 30%);
    color: white;
    background-color: #535252;
    margin: 20px auto 0 auto;
    padding: 0 20px 20px 20px;
}

.top,.bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.city{
    font-weight: 600;
    font-size:  18px;
    line-height: 1;
    margin: 0;
    letter-spacing: 1px;
}

.weather-description{
    font: weight 400px;
    font-size: 14px;
    line-height: 1;
    margin: 0;
}

.weather-icon{
    width: 100px;

}

.temperature{
    font-weight: 600;
    font-size: 50px;
    width: auto;
    letter-spacing: -5px;
    margin:10px 0 ;
}

.details{
    width: 100%;
    padding-left: 10px;

}

.parameter-row{
    display: flex;
    justify-content: space-between;
}

.arameter-label{
    text-align: left;
    font-weight: 400;
    font-size: 12px;
}
.parameter-value{
    text-align: right;
    font-weight: 600;
    font-size: 16px;
}

.parameter-label-top{
    border-bottom: 1px solid white;
    font-size: 20px;
}